import React from 'react'
import "../sass/main.scss"
import "../sass/games.scss"

import Layout from '../layouts/main'

class Post extends React.Component {
    render() {
        return  <Layout>

                    <div className="notice">
                        <p><b>This section is in progress.</b></p>
                        <p>I'm currently cleaning up some of my old gamedev projects and porting them to itch.io. Keep an eye on this page as this is where I'll be listing them.</p>
                    </div>

                    <div className="game">
                        <a href="https://ldjam.com/events/ludum-dare/41/tower-defense-dating-simulator" target="_blank">
                            <img className="game-image" src="/static/PuzzleDungeon.png" height="200" width="200"></img>
                        </a>
                        <div className="game-details">
                            <div className="game-title">
                                <a href="https://ldjam.com/events/ludum-dare/41/tower-defense-dating-simulator" target="_blank">Tower Defense Dating Simulator</a>
                                <i title="PC" className="fab fa-windows"></i>
                                <i title="Mac" className="fab fa-apple"></i>
                            </div>
                            <div className="game-categories">
                                <div className="game-category">3D</div>
                                <div className="game-category">Action</div>
                            </div>
                            <div className="game-description">
                                <p>Made in 48 hours for Ludum Dare 41, this game mixes tower defense with dating simulator genres to deliver a unique and hectic romance game.</p>
                            </div>
                        </div>
                    </div>

                    <div className="game">
                        <a href="https://killianmccabe.github.io/PuzzleDungeon/" target="_blank">
                            <img className="game-image" src="/static/PuzzleDungeon.png" height="200" width="200"></img>
                        </a>
                        <div className="game-details">
                            <div className="game-title">
                                <a href="https://killianmccabe.github.io/PuzzleDungeon/" target="_blank">Puzzle Dungeon</a>
                                <span className="game-platforms">
                                    <i title="HTML5 (web)" className="fab fa-html5"></i>
                                </span>
                            </div>
                            <div className="game-categories">
                                <div className="game-category">2D</div>
                                <div className="game-category">Puzzle</div>
                            </div>
                            <div className="game-description">
                                <p>Explore a dungeon filled with puzzles. Each puzzle is harder than the ones before. Can you reach the end?</p>
                            </div>
                        </div>
                    </div>

                    <div className="game">
                        <a href="https://killianmccabe.github.io/MemoryGame/" target="_blank">
                            <img className="game-image" src="/static/Memory.png" height="200" width="200"></img>
                        </a>
                        <div className="game-details">
                            <div className="game-title">
                                <a href="https://killianmccabe.github.io/MemoryGame/" target="_blank">Memory</a>
                                <span className="game-platforms">
                                    <i title="HTML5 (web)" className="fab fa-html5"></i>
                                </span>
                            </div>
                            <div className="game-categories">
                                <div className="game-category">Puzzle</div>
                            </div>
                            <div className="game-description">
                                <p>Train your brain by memorising cards and matching pairs.</p>
                            </div>
                        </div>
                    </div>
                </Layout>
    }
}

export default Post